.Member {
    background-color: rgb(43, 45, 49);
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.Member .avatar {
    user-select: none;
    pointer-events: none;
    margin: 5px;
    height: 40px;
    border-radius: 100%;
}

.Member .nameWrapper {
    height: 100%;
    margin: 5px;
    display: flex;
    flex-direction: column;
    color: rgb(149, 155, 163);
}

.Member .name {
    margin: 0%;
}