.Message {
    background-color: rgb(49, 51, 56);
    display: flex;
    align-content: stretch;
    align-items: stretch;
}

.Message .avatar {
    user-select: none;
    pointer-events: none;
    margin: 10px;
    margin-right: 5px;
    height: 50px;
    border-radius: 100%;
}

.Message .content {
    color: rgb(219, 221, 224);
    margin: 10px;
    margin-left: 5px;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.Message .content .message {
    margin: 0%;
}

.Message .content .meme {
    max-height: 250px;
    user-select: none;
    pointer-events: none;
    border-radius: 10px;
}
